<template>
  <div>
    <LayoutBox one="添加商品">
      <h4>商品信息填写</h4>
      <el-divider></el-divider>
      <el-form ref="form" :model="form" :rules="rules" label-width="130px">
        <!-- <el-form-item label="分类：" prop="activityType">
          <el-select v-model="form.activityType" placeholder="请选择分类" @change="onActivityType">
            <el-option label="普通商品" :value="2"></el-option>
            <el-option label="秒杀" :value="0"></el-option>
            <el-option label="特惠" :value="1"></el-option>
            <el-option label="拼团" :value="3"></el-option>
            <el-option label="预售" :value="10"></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="名称：" prop="name">
          <el-input v-model="form.name" placeholder="请输入名称" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="编码：" prop="code">
          <el-input v-model="form.code" placeholder="请输入商品编码" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="商品分类：" prop="categoryId">
          <el-cascader v-model="form.categoryId" :options="sortList" />
        </el-form-item>
        <el-form-item label="原价：" prop="originalPrice">
          <el-input v-model="form.originalPrice" placeholder="请输入原价" style="width: 200px"></el-input>
        </el-form-item>
        <!-- <el-form-item label="现价：" prop="price">
          <el-input v-model="form.price" placeholder="请输入现价" style="width: 200px"></el-input>
        </el-form-item> -->
        <el-form-item label="重量：" prop="weight">
          <el-input v-model="form.weight" placeholder="请输入重量" style="width: 200px">
            <template slot="append">kg</template>
          </el-input>
        </el-form-item>
        <!-- <el-form-item v-if="form.activityType === 3" label="拼团有效时间：" required>
          <el-input v-model="form.spellGroupTime" placeholder="请输入拼团有效时间" style="width: 200px">
            <template slot="append">h</template>
          </el-input>
        </el-form-item>
        <el-form-item v-if="form.activityType === 3" label="拼团人数：" required>
          <el-input v-model="form.activityNumber" placeholder="请输入拼团人数" style="width: 200px" />
        </el-form-item>
        <el-form-item v-if="form.activityType === 10" label="预售开始：" required>
          <el-date-picker v-model="form.presellProdInfo.createTime" type="datetime" placeholder="选择预售开始时间" />
        </el-form-item>
        <el-form-item v-if="form.activityType === 10" label="预售结束：" required>
          <el-date-picker v-model="form.presellProdInfo.endTime" type="datetime" placeholder="选择预售结束时间" />
        </el-form-item>
        <el-form-item v-if="form.activityType === 10" label="几天后发货：" required>
          <el-input v-model="form.presellProdInfo.sendTime" placeholder="几天后发货" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item v-if="form.activityType === 10" label="退款比例：" required>
          <el-input v-model="form.presellProdInfo.refundPrice" placeholder="退款比例" style="width: 200px"></el-input>
          <span style="color: #ccc"> 注：50%就输入0.5即可</span>
        </el-form-item>
        <el-form-item v-if="form.activityType === 10" label="预售数量：" required>
          <el-input v-model="form.presellProdInfo.amount" placeholder="请输入预售数量" style="width: 200px"></el-input>
        </el-form-item> -->
        <el-form-item label="商品图片：" required>
          <el-upload action="http-request" :http-request="onUpload" list-type="picture-card" :on-remove="onRemove" :file-list="imageUrlList" :limit="5" multiple>
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="商品描述：" prop="title">
          <el-input v-model="form.title" type="textarea" placeholder="请输入商品描述"></el-input>
        </el-form-item>
        <el-divider></el-divider>
        <el-form-item>
          <el-button v-if="id" class="inquire" type="success" round @click="onSave('form')">保存</el-button>
          <el-button v-else class="inquire" type="success" round @click="onSubmit('form')">提交</el-button>
          <router-link to="/superMarket/goodsList"><el-button type="danger" style="width: 112px" round>取消</el-button></router-link>
        </el-form-item>
      </el-form>

    </LayoutBox>

    <image-compressor-cmp ref="imageCompressorCmp" />
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import imageCompressorCmp from '@/components/image_compressor_cmp/index.vue'
import { MIXINS_QUERYPARENTNOPAGE_API } from '@/mixins/api.js'

export default {
  name: 'GoodsAdd',

  components: {
    LayoutBox,
    imageCompressorCmp
  },

  mixins: [
    MIXINS_QUERYPARENTNOPAGE_API,
  ],

  data () {
    let validateDouble = (rule, value, callback) => {
      let patrn = /^\d+(\.\d+)?$/
      if (!patrn.exec(value)) {
        callback(new Error('只能输入数字'))
      }
    }
    return {
      id: '', // 编辑的时候获取的id
      itemId: '', // 编辑时获取的明细id
      headId: '', // 编辑时获取的headId
      imageUrlList: [],
      form: {
        // activityType: '', // 活动类型 0-秒杀 1-特惠 2-常规
        name: '', // 商品名称
        categoryId: '', // 选择的分类id
        originalPrice: '', // 原价
        // price: '', // 现价
        weight: '', // 重量
        title: '', // 商品描述
        type: 1, // 小超：1 woho：0
        spellGroupTime: '', // 拼团有效时间
        activityNumber: '', // 拼团人数
        presellProdInfo: { // 预售信息
          amount: '', // 预售数量
          refundPrice: '', // 退款比例
          createTime: '', // 开始时间
          endTime: '', // 结束时间
          sendTime: '', // 预计几天后发货
        }
      },
      rules: {
        // activityType: [
        //   { required: true, message: '请选择活动类型', trigger: 'change' }
        // ],
        name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        categoryId: [
          { required: true, message: '请输入品类名称', trigger: 'blur' }
        ],
        originalPrice: [
          { required: true, message: '请输入原价', trigger: 'blur' },
          { validator: validateDouble, trigger: 'blur' }
        ],
        // price: [
        //   { required: true, message: '请输入现价', trigger: 'blur' },
        //   { validator: validateDouble, trigger: 'blur' }
        // ],
        weight: [
          { required: true, message: '请输入重量', trigger: 'blur' }
        ],
        title: [
          { required: true, message: '请输入商品描述', trigger: 'blur' }
        ]
      },
      sortList: [] // 所有分类
    }
  },

  methods: {
    // 点击提交
    onSubmit (formName) {
      const data = this._formRules(formName)
      data.price = this.form.originalPrice
      if (data) {
        this.apiAddGoods(data)
      }
    },
    // 点击保存
    onSave (formName) {
      const data = this._formRules(formName)
      data.id = this.itemId
      data.headId = this.headId
      if (data) {
        this.apiUpdateGoods(data)
      }
    },
    // 自定义覆盖图片上传方法
    async onUpload (params) {
      const file = params.file
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
      if (!isJPG) {
        this.$message.error('上传的图标只能是 JPG/PNG 格式!')
        // this.imageUrlList = []
        return
      }
      await this.apiUpload(file)
    },
    // 移除图片时的钩子
    onRemove (file, fileList) {
      this.imageUrlList = fileList
    },
    // // 切换分类时触发
    // onActivityType (e) {
    //   if (e !== 10) {
    //     this.form.presellProdInfo = {}
    //   }
    // },

    // 私有方法 ------------------------------------
    // 表单验证
    _formRules (formName) {
      let data = true
      // switch (this.form.activityType) {
      //   case 3:
      //     if (!this.form.spellGroupTime) return this.$message.error('拼团有效时间不能为空')
      //     if (!this.form.activityNumber) return this.$message.error('拼团人数不能为空')
      //     break
      //   default:
      //     break
      // }
      if (this.imageUrlList.length < 1) {
        this.$message.error('请选择图片')
        data = false
        return data
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          data = true
        } else {
          data = false
        }
      })
      if (data) {
        data = this.form
        let i = ''
        this.imageUrlList.map((item) => {
          let imageUrlKeyName = 'imageUrl' + i
          data[imageUrlKeyName] = item.url
          i = Number(i) + 1
        })
        let categoryIds = data.categoryId.map((item) => {
          return item.id
        })
        categoryIds = categoryIds.join(',')
        let category = data.categoryId[data.categoryId.length - 1]
        let categoryData = {
          categoryIds: categoryIds,
          categoryId: category.id,
          categoryName: category.categoryName
        }
        data = { ...data, ...categoryData }
      }
      return data
    },

    // 分类递归json重组
    _sortRecursive (level, parentId, list, maxLevel) {
      if (maxLevel < level) {
        return false
      } else {
        let letName = 'level' + level
        this[letName] = []
        this[letName] = list.filter((item) => {
          return item.level === level && item.parentId === parentId
        })
        return this[letName].map((item) => {
          return {
            value: item,
            label: item.categoryName,
            children: this._sortRecursive(level + 1, item.id, list, maxLevel),
          }
        })
      }
    },

    // api---------------------------------------------------------
    // 添加商品
    apiAddGoods (data) {
      this.$http.post('/woho/small/product/addProd', data)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('保存成功')
            this.$router.push('/superMarket/goodsList')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 编辑商品
    apiUpdateGoods (data) {
      this.$http.put('/woho/small/product/updateSmallProd', data)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('保存成功')
            this.$router.push('/superMarket/goodsList')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 获取分类
    apiGetSortList () {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      this.queryParentNoPage({}).then((res) => {
        loading.close()
        if (res.data.success) {
          this.initSortList = res.data.data
          this.sortList = this._sortRecursive(1, '0', res.data.data, 3)
        }
      })
    },

    // 图片上传
    async apiUpload (file) {
      const formData = await this.$refs.imageCompressorCmp._imageCompressor(file)
      this.$http.post('/common/oss/uploadFile', formData)
        .then((res) => {
          if (res.data.success) {
            this.imageUrlList.push({ url: res.data.data.webUrl })
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 编辑时获得的数据
    apiFindIdGoods () {
      this.$http.get(`/woho/small/product/getSmallProdById/${this.id}`)
        .then((res) => {
          this.form = res.data.data
          this.itemId = this.form.id
          let category3 = []
          let category2 = []
          let category1 = []
          category3 = this.initSortList.filter((item) => {
            return item.id === this.form.categoryId
          })
          category2 = this.initSortList.filter((item) => {
            return item.id === category3[0].parentId
          })
          category1 = this.initSortList.filter((item) => {
            return item.id === category2[0].parentId
          })
          this.form.categoryId = []
          this.form.categoryId[0] = category1[0]
          this.form.categoryId[1] = category2[0]
          this.form.categoryId[2] = category3[0]
          for (let i = ''; i < 5;) {
            const imageUrlkeyName = 'imageUrl' + i
            if (this.form[imageUrlkeyName]) {
              this.imageUrlList.push({ url: this.form[imageUrlkeyName] })
            }
            i = Number(i) + 1
          }
        })
    }
  },

  created () {
    this.apiGetSortList()
    this.id = this.$route.query.id
    setTimeout(() => {
      if (this.id) {
        this.apiFindIdGoods()
      }
    }, 1000)
  },
}
</script>

